.footer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  position: fixed;
  bottom: 0;
  background-color: rgb(122, 12, 155);
  color: rgb(169, 170, 215);
  text-align: center;
  align-items: center;
  width: 100%;
  height: 30px;
  padding: 5px 0 10px 0;
  font-weight: bold;
  border-top: rgb(18, 19, 15) solid 3px;
}

.footer img {
  width: 20px;
  padding: 0 5px;
}

.icon {
  font-size: 10px;
}
.icon a {
  font-size: 10px;
}

@media screen and (max-width: 600px) {
  .icon,
  .icon a {
    font-size: 7px;
  }
}

@media screen and (max-width: 420px) {
  .footer {
    font-size: 12px;
  }
  .icon,
  .icon a {
    font-size: 5.5px;
  }
}